.filter_wrap {
	width: 10vw;
	padding: 2vh 2vw 0 0;
	> div {
		width: 100%;
		height: 15vh;
		border-bottom: 2px solid #aaa;
		margin-bottom: 20px;
		.filter_tit {
			width: 100%;
			font: bold 1.2vw/1 'Phudu';
			letter-spacing: 1.5px;
			margin-bottom: 1.5vh;
			display: flex;
			justify-content: space-between;
			.close {
				font-size: 1.2vw;
			}
		}
		.filter_sublist {
			display: flex;
			flex-direction: column;
			gap: 0.3vh;
			label {
				width: 100%;
				margin-bottom: 0.3vh;
				&:hover {
					span {
						color: #111;
					}
				}
				input {
					width: 0.7vw;
					height: 0.7vw;
				}
				span {
					font: 0.8vw/1 'arial';
					color: #aaa;
				}
			}
		}
	}
}
