.headerBox {
	width: 100%;
	height: 6vw;
}
header {
	width: 100vw;
	height: 6vw;
	display: flex;
	justify-content: space-evenly;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 2;
	background-color: #fff;
	transition: 0.5s 0.2s;
	border-bottom: 1px solid #aaa;
	overflow: hidden;
	h1 {
		width: 15vw;
		height: 75%;
		position: absolute;
		left: 10%;
		top: 50%;
		transform: translate(-50%, -50%);
		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			cursor: pointer;
		}
	}
	#gnb {
		display: flex;
		justify-content: space-evenly;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		li {
			width: 10vw;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			a {
				font: bold 1.2vw/1 'Phudu';
				letter-spacing: 1px;
				position: relative;
				&::after {
					content: '';
					width: 0%;
					height: 2px;
					background-color: #fff;
					opacity: 0;
					position: absolute;
					bottom: -20px;
					left: 0%;
					transition: 0.5s;
				}
				&:hover {
					&::after {
						width: 100%;
						opacity: 1;
						background-color: #333;
					}
				}
			}
		}
	}
	.util {
		width: 20vw;
		height: 100%;
		display: flex;
		justify-content: space-between;
		position: absolute;
		top: 50%;
		right: 3vw;
		transform: translateY(-50%);
		li {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			a {
				&:first-child {
					font-weight: bold;
				}
				transform: scale(0.9);
				font: 0.8vw/1 'Nanum Gothic';
			}
		}
	}
	.bars {
		width: 100%;
		height: 100%;
		position: relative;
		display: none;
		.fa-bars {
			width: 20px;
			height: 20px;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20px;
			position: absolute;
			top: 50%;
			right: 20px;
			transform: translateY(-50%);
		}
	}
}
aside {
	width: 100vw;
	height: 100vh;
	position: absolute;
	z-index: 5;
	top: 0;
	right: -200%;
	background-color: rgba($color: #000000, $alpha: 0.3);
	display: flex;
	flex-direction: row-reverse;
	transition: 1s, transform 0.2s, opacity 0.1s;
	opacity: 0;
	transform: scale(0);
	&.on {
		transform: scale(1);
		right: 0%;
		opacity: 1;
	}
	.inner {
		width: 85%;
		height: 100%;
		background-color: #ddd;
		position: relative;
		.close {
			width: 15%;
			height: 15vw;
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 10px;
			left: -16%;
			.fa-x {
				font-size: 10vw;
			}
		}
		#gnb {
			width: 80%;
			padding: 10px 0;
			position: absolute;
			top: 100px;
			left: 40px;
			display: flex;
			flex-direction: column;
			gap: 30px;
			li {
				display: block;
				width: 100%;
				border-bottom: 1px solid #aaa;
				a {
					font: bold 18px/1 'Phudu';
				}
			}
		}
		.util {
			width: 100%;
			height: 70px;
			position: absolute;
			top: 0px;
			left: 0px;
			background-color: #333;
			display: flex;
			align-items: center;
			gap: 20px;
			padding-left: 40px;
			li {
				color: #eee;
				a {
					font: 16px/1 'Phudu';
					letter-spacing: -1px;
					color: #eee;
				}
				i {
					display: none;
				}
			}
		}
	}
}

@media screen and (max-width: 450px) {
	.headerBox {
		height: 0px;
		&.on {
			height: 0px;
		}
	}
	header {
		width: 100vw;
		height: 70px;
		position: relative;
		h1 {
			width: 200px;
			height: 80%;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			img {
				position: absolute;
				width: 100%;
				height: 100%;
				object-fit: contain;
				cursor: pointer;
			}
		}
		#gnb {
			display: none;
		}
		.util {
			display: none;
		}
		.bars {
			display: block;
		}
	}
}
